@import url(https://fonts.googleapis.com/css2?family=Bokor&family=Open+Sans&display=swap);
#home {
  background-image: url(/bridge-in-tech-web/static/media/bg.b23c09a5.png);
  background-size: 100%;
  height: 80vh;
}
.header-text {
  max-width: 350px;
  margin-top: 30px;
  padding-top: 50px;
  margin-left: 16%;
}

.header-text h1 {
  color: #007bff;
  text-align: left;
  font-size: 35px;
  width: 500px;
  padding: 0;
  margin: 0 !important;

  font-family: "Tahoma";
}
#home p {
  margin-top: 20px;
  color: rgb(87, 85, 85);
  font-size: 15px;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 1px;
}
@media only screen and (max-width: 700px) {
  .header-text h1 {
    font-size: 20px !important;
    width: 300px !important;
  }
  #home {
    background-image: none;
  }
}

.container-members {
    background: #f7f9fa;
    height: auto;
    width: 90vw;
    margin: 5em auto;
    box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.4);
  }
  
label {
    color: #24b9b6;
    font-size: 1.2em;
    font-weight: 400;
  }
  
  h1 {
    color: #24b9b6;
    padding-top: 0.5em;
  }
  
  .error {
    color: red;
  }
  .error-message {
    color: #ff6565;
    padding: 0.5em 0.2em;
    height: 1em;
    position: absolute;
    font-size: 0.8em;
  }
  
  .field {
    border: 1px solid #1c6ea4;
    width: 100%;
    height: 2em;
  }
  
  .button-group {
    width: 100%;
  }
  
  .membersList {
    width: 85vw;
    text-align: center;
    padding: 2em;
    margin: 3em 3em;
    padding: 2em;
  }
  
 * {
    margin: 0;
    padding: 0;
 }
 
 .errorPage {
     position: absolute;
     top: 15%;
     left: 17%;
     right: 17%;
     color: whitesmoke;
     bottom: 20%;
     display: -webkit-flex;
     display: flex;
     -webkit-align-items: center;
             align-items: center;
     -webkit-justify-content: center;
             justify-content: center;
 }
 
 .errorPage_content {
     max-width: 600px;
     text-align: center;
 }
 
 .errorPage_content h1 {
     color: #57d5ff;
     font-size: 10vw;
     font-weight: bolder;
     line-height: 1em;
 }
 
 .errorPage_content h4 {
     font-size: 2em;
     margin-bottom: 20px;
     text-transform: uppercase;
     max-width: 600px;
     color: #0d0d0d;
 }
 
 .errorPage_content p {
    font-size: 1.4em;
    color: grey;
 }
 
 .button {
     margin-top: auto;
     padding: 0.4rem;
     border-radius: .25rem;
     border-color: #57d5ff;
 }
 
